.footer__social-list-item-link-icon-inner { overflow: hidden; position: relative; display: block; width: 100%; height: 100%; }

.footer__social-list-item-link { color: #F2C71A; display: inline-flex; align-items: center; justify-content: flex-start; font-family: Poppins-Regular; }

.footer__social-list-item-link svg { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 30px; height: 30px; }

.footer__social-list-item-link:hover svg { animation: arrowAnimation cubic-bezier(0.78, -0.03, 0.2, 0.75) 1.2s infinite; }

.footer__copyright { text-align: center; font-family: Poppins-Thin; color: #fff; }

@keyframes arrowAnimation { from { transform: translate(-50%, -50%);
    opacity: 1; }
  40% { transform: translate(-50%, -50%); }
  50% { opacity: 0; }
  60% { transform: translate(-35px, -50%);
    opacity: 0; }
  to { transform: translate(-50%, -50%);
    opacity: 1; } }
